import React, { useCallback, useEffect, useMemo, useState } from 'react'
import { IconButton, Typography } from '@mui/material/'
import { DragDropContext, Draggable, Droppable } from 'react-beautiful-dnd'
import AddIcon from '@mui/icons-material/Add'
import uuid from 'uuid/dist/v4'
import Methods from 'components/Methods'
import {
  Accordion,
  AccordionDetails,
  AccordionSummary,
  Approve,
  FlexWrap,
  Title,
  Wrapper,
} from 'components/CommonComponent'
import DataWrapper from 'components/DataWrapper'
import usePanel from 'hooks/usePanel'
import { useWeb3React } from 'web3-react-core'
import { styled } from '@mui/material/styles'
import PositionABI from 'config/abi/Position.json'
import { AvatarIcon, Button, DeleteIcon, DroppableIcon } from 'components/uikit'
import { getContract } from 'utils/web3React'
import { utils } from 'ethers'
import useSystemParams from 'hooks/useSystemParams'
import contracts from 'config/constants/contracts'
import { TokenUtils, toLargeUnits, toSmallUnits } from 'utils/transformHelper'
import useToast from 'hooks/useToast'
import { useComputedIndexAction, usePositionAction, usePositionInfo } from 'hooks/news/usePoistion'
import Logo from 'components/Logo'
import { DataIndexName, ToastTitle } from 'config/constants/contants'
import { findPath, getSwapQuote } from 'config/constants/pool'
import { SupportedTokens } from 'config/constants/tokenConfig'
import { TransactionSubmittedInfo, TransactionSuccessInfo } from 'components/TransactionInfo'

const AddBtn = styled(Button)`
  box-shadow: none !important;
  width: 34px !important;
  height: 28px !important;
  padding: 0 !important;
  background: linear-gradient(90deg, #62c3f7 0%, #7ae9f1 100%) !important;
  border-radius: 6px !important;
  svg {
    color: #000;
    width: 16px;
  }
`

const { ActionUser, ActionMulticall } = contracts
const ActionUserInterface = new utils.Interface(ActionUser.abi)
const ActionMulticallInterface = new utils.Interface(ActionMulticall.abi)
const onDragEnd = (result, columns, setColumns, setIsSubmit) => {
  if (!result.destination) return
  const { source, destination } = result
  if (source.droppableId !== destination.droppableId) {
    const sourceColumn = columns[source.droppableId]
    const destColumn = columns[destination.droppableId]
    const sourceItems = [...sourceColumn.items]
    const destItems = [...destColumn.items]
    const [removed] = sourceItems.splice(source.index, 1)
    destItems.splice(destination.index, 0, removed)
    setColumns([...destColumn, destItems])
  } else {
    const copiedItems = [...columns]
    const [removed] = copiedItems.splice(source.index, 1)
    copiedItems.splice(destination.index, 0, removed)
    if (copiedItems.map((it) => it.input > 0).includes(false)) {
      setIsSubmit(true)
    } else {
      setIsSubmit(false)
    }
    setColumns(copiedItems)
  }
}


const TradingThrough: React.FC<{
  onApprove: (callback: (state: boolean)=> void) => void
  approveStatus: boolean
}> = (props) => {
  const { onApprove, approveStatus } = props
  const { chainId } = useWeb3React()
  const { toastInfo, toastError, toastSuccess } = useToast()
  const { signer, tokenPools } = useSystemParams()
  const [columns, setColumns] = useState<
    {
      id: string
      input: number
      output: number
      inputToken: {
        token: SupportedTokens
        balance: number
        decimals: number
        icon: React.FC<JSX.Element>
        rightOptionList: {
          token: SupportedTokens
          balance: number
          decimals: number
          icon: React.FC<JSX.Element>
        }[]
      }
      outputToken: { token: SupportedTokens; balance: number; decimals: number; icon: React.FC<JSX.Element> }
    }[]
  >([])
  const [isSubmitStatus, setIsSubmit] = useState(true)
  const [approveBtnStatus, setApproveBtnStatus] = useState(false)
  const {
    onUpdateTotalCollateralValue,
    newDisplayCollateralRatio,
    newCollateralRatio,
    newDisplayPrepaidInterestCoverage,
  } = useComputedIndexAction()
  const { handleUpdatePosition } = usePositionAction()
  const { currentPositionTokenInfo, collateralIndex, currentPosition } = usePositionInfo()

  const tokenList = useMemo<
    {
      token: SupportedTokens
      balance: number
      decimals: number
      icon: React.FC<JSX.Element>
      rightOptionList: {
        token: SupportedTokens
        balance: number
        decimals: number
        icon: React.FC<JSX.Element>
      }[]
    }[]
  >(() => {
    if (currentPositionTokenInfo?.tokenPrice) {
      const { allowedTokens } = currentPositionTokenInfo
      const list = Object.values(allowedTokens.tokens).map((it) => {
        const balance =
          (allowedTokens?.balances &&
            (toLargeUnits(allowedTokens?.balances[it.symbol] || 0, it.decimals, true) as number)) ||
          0
        return {
          decimals: it.decimals,
          token: it.symbol,
          balance,
          icon: () => <Logo imgprops={{ style: { marginRight: 10 } }} token={it.logo} symbol={it.symbol} />,
          rightOptionList: [],
        }
      })
      return list.map((item) => {
        const rightOptionList = list.filter((it) => it.token !== item.token);
        return {
          ...item,
          rightOptionList: rightOptionList.length ? rightOptionList : [item],
        }
      })
    }
    return []
  }, [currentPositionTokenInfo])
  const addMethods = useCallback(() => {
    setColumns((state) => {
      const data = [
        ...state,
        {
          id: uuid(),
          input: 0,
          output: 0,
          inputToken: tokenList[0],
          outputToken: tokenList[0].rightOptionList[0],
        },
      ]
      if (data.map((it) => it.input > 0).includes(false)) {
        setIsSubmit(true)
      } else {
        setIsSubmit(false)
      }
      return data
    })
  }, [tokenList])

  const onSubmit = useCallback(async () => {
    const posContract = getContract(signer, PositionABI, currentPosition.positionAddress)
    try {
      if (posContract) {
        const isSubmit = columns.filter((it) => it.input <= 0)
        if (isSubmit.length) {
          toastError(ToastTitle.failed, 'Please enter a valid amount')
          return
        }

        setIsSubmit(true)
        const _params = columns.map((it) => {
          const inputAmount = toSmallUnits(String(it.input), it.inputToken.decimals)
          const outputAmount = toSmallUnits(String(it.output), it.outputToken.decimals)
          const { tokenAddressPath } = findPath({
            chainId,
            from: it.inputToken.token,
            to: it.outputToken.token,
          })

          return [
            ActionUser.address[chainId],
            ActionUserInterface.encodeFunctionData('swapExactTokensForTokens', [
              inputAmount,
              outputAmount,
              tokenAddressPath,
              '999999999999999',
            ]),
          ]
        })
        const dataMulti = ActionMulticallInterface.encodeFunctionData('multicall', [_params])
        const transaction = await posContract.delegateCall(ActionMulticall.address[chainId], dataMulti)
        if (transaction.hash) {
          toastInfo(
            ToastTitle.submited,
            <TransactionSubmittedInfo content="Waiting for confirmation" txHash={transaction.hash} />,
          )
        }
        const result = await transaction.wait()
        if (result) {
          setIsSubmit(true)
          onUpdateTotalCollateralValue(0)
          toastSuccess(
            ToastTitle.success,
            <TransactionSuccessInfo content="The transaction has been successfully." txHash={transaction.hash} />,
          )
          try {
            await handleUpdatePosition()
          } catch (error) {
            console.error('handleUpdatePosition>>>>', error)
          }
        }
      }
    } catch (error: any) {
      setIsSubmit(false)
      toastError(
        'Failed',
        <FlexWrap style={{ flexDirection: 'column' }}>
          <p
            style={{
              wordBreak: 'break-all',
            }}
          >
            {error.reason}
          </p>
        </FlexWrap>,
      )
    }
  }, [
    signer,
    currentPosition.positionAddress,
    columns,
    chainId,
    toastInfo,
    onUpdateTotalCollateralValue,
    toastSuccess,
    handleUpdatePosition,
    toastError,
  ])

  useEffect(() => {
    console.log('tokenList', tokenList)
    if (tokenList.length) {
      setColumns((state) => {
        if (state.length) {
          return state;
        }
        return [
          {
            id: uuid(),
            input: 0,
            output: 0,
            inputToken: tokenList[0],
            outputToken: tokenList[0].rightOptionList[0],
          },
        ]
      })
    }
  }, [tokenList])
  const onChangeInputAmount = useCallback(
    (
      e,
      item: {
        id: string
        input: number
        output: number
        inputToken: {
          token: SupportedTokens
          balance: number
          decimals: number
          icon: React.FC<JSX.Element>
          rightOptionList: {
            token: SupportedTokens
            balance: number
            decimals: number
            icon: React.FC<JSX.Element>
          }[]
        }
        outputToken: { token: SupportedTokens; balance: number; decimals: number; icon: React.FC<JSX.Element> }
      },
    ) => {
      const value = TokenUtils.formatTokenAmount(Number(e.target.value), item.inputToken.decimals)
      if (Number(value) > Number.MAX_SAFE_INTEGER) {
        setIsSubmit(true)
        return
      }
      const res =
        getSwapQuote(
          chainId,
          item.inputToken.token,
          item.outputToken.token,
          toSmallUnits(value, item.inputToken.decimals),
          tokenPools,
        ) || 0
      setColumns((state) => {
        const data = state.map((it) => {
          const _item = {
            ...it,
            input: it.id === item.id ? e.target.value : it.input,
            output: it.id === item.id ? (toLargeUnits(res, item.outputToken.decimals, true) as number) : it.output,
          }
          return _item
        })

        const actionValSum = data.reduce((acc, curr) => {
          const next = { ...acc }
          if (Object.prototype.hasOwnProperty.call(next, curr.inputToken.token)) {
            next[curr.inputToken.token] += Number(curr.input)
          } else {
            next[curr.inputToken.token] = Number(curr.input)
          }
          return next
        }, {} as Record<SupportedTokens, number>)

        if (data.map((it) => it.input > 0 && it.output > 0 && it.input <= it.inputToken.balance).includes(false)) {
          setIsSubmit(true)
        } else if (
          value.indexOf('.') !== -1 &&
          String(value).slice(value.indexOf('.') + 1).length > item.inputToken.decimals
        ) {
          setIsSubmit(true)
        } else {
          setIsSubmit(false)
        }

        if (data.map((it) => it.input > 0).includes(false)) {
          setIsSubmit(true)
        } else if (actionValSum[item.inputToken.token] > item.inputToken.balance) {
          setIsSubmit(true)
        } else {
          setIsSubmit(false)
        }
        return data
      })
    },
    [chainId, tokenPools],
  )
  const onChangeToken = useCallback(
    (selected, id, type) => {
      setColumns((state) => {
        return state.map((item) => {
          if (item.id === id) {
            const data = { ...item, [type]: selected }
            if (data.input) {
              const res = getSwapQuote(
                chainId,
                data.inputToken.token,
                data.outputToken.token,
                toSmallUnits(
                  TokenUtils.formatTokenAmount(data.input, data.inputToken.decimals),
                  data.inputToken.decimals,
                ),
                tokenPools,
              )
              data.output = toLargeUnits(res, data.outputToken.decimals, true) as number
            }
            return data
          }
          return item
        })
      })
    },
    [chainId, tokenPools],
  )
  const onExchange = useCallback((record, index) => {
    setColumns((state) => {
      return state.map((item, i) => {
        if (i === index) {
          const data: {
            token: SupportedTokens
            balance: number
            decimals: number
            icon: React.FC<JSX.Element>
            rightOptionList: {
              token: SupportedTokens
              balance: number
              decimals: number
              icon: React.FC<JSX.Element>
            }[]
          } = {
            ...item.outputToken,
            rightOptionList: [],
          }
          data.rightOptionList = [...item.inputToken.rightOptionList]
          data.rightOptionList.push(item.inputToken)
          data.rightOptionList = data.rightOptionList.filter((it) => it.token !== item.outputToken.token)
          const target = {
            ...item,
            outputToken: item.inputToken,
            inputToken: { ...data, balance: item.outputToken.balance },
            input: 0,
            output: 0,
          }
          return { ...target }
        }
        return item
      })
    })
  }, [])

  useEffect(() => {
    const { tokenPrice } = currentPositionTokenInfo
    if (tokenPrice) {
      const poolPath = columns.map((it) => ({
        from: it.inputToken.token,
        to: it.outputToken.token,
        fromDecimals: it.inputToken.decimals,
        toDecimals: it.outputToken.decimals,
        inputValue: Number(it.input),
        outputValue: Number(it.output),
      }))
      let total = 0
      poolPath.forEach((item) => {
        total -=
          item.inputValue *
          (toLargeUnits(
            tokenPrice[item.from].price,
            currentPosition.mintTokenInfo.settlementCurrency.decimals,
            true,
          ) as number)
        total +=
          item.outputValue *
          (toLargeUnits(
            tokenPrice[item.to].price,
            currentPosition.mintTokenInfo.settlementCurrency.decimals,
            true,
          ) as number)
      })
      onUpdateTotalCollateralValue(total)
    }
  }, [
    columns,
    currentPosition.mintTokenInfo.settlementCurrency.decimals,
    currentPositionTokenInfo,
    onUpdateTotalCollateralValue,
  ])
  return <>
    <DragDropContext onDragEnd={(result) => onDragEnd(result, columns, setColumns, setIsSubmit)}>
      <Droppable droppableId="droppableId">
        {(provided) => {
          return (
            <div {...provided.droppableProps} ref={provided.innerRef}>
              {columns.map((item, index) => {
                return (
                  <Draggable key={item.id} draggableId={item.id} index={index} isDragDisabled={index === 0}>
                    {(provideds) => {
                      const amountOut =
                        (tokenPools.length &&
                          getSwapQuote(
                            chainId,
                            item.inputToken.token,
                            item.outputToken.token,
                            toSmallUnits(String(Number(TokenUtils.formatTokenAmount(item.input, item.inputToken.decimals)) || '1'), item.inputToken.decimals),
                            tokenPools,
                          )) ||
                        0
                      const price = toLargeUnits(amountOut, item.outputToken.decimals, true) as number
                      const unitAmountOut = price / (Number(item.input) || 1)

                      const priceStr = TokenUtils.formatFourDecimals(unitAmountOut)
                      return (
                        <div
                          ref={provideds.innerRef}
                          {...provideds.draggableProps}
                          {...provideds.dragHandleProps}
                          style={{
                            display: 'flex',
                            alignItems: 'center',
                            userSelect: 'none',
                            margin: '0 0 8px 0',
                            ...provideds.draggableProps.style,
                            position: 'static',
                          }}
                        >
                          <div style={{ width: '100%', position: 'relative', paddingRight: index <= 0 ? 58 : 0 }}>
                            <Methods
                              SelectIcon={AvatarIcon}
                              leftInputValue={item.input}
                              rightInputValue={item.output}
                              isExchange
                              onExchange={() => {
                                onExchange(item, index)
                              }}
                              options={tokenList}
                              leftSelectValue={item.inputToken.token}
                              leftBalance={item.inputToken.balance}
                              rightSelectValue={item.outputToken.token}
                              rightBalance={item.outputToken.balance}
                              rightOptions={item.inputToken.rightOptionList}
                              selectedBalance={100}
                              leftDisableStatus={[false, false]}
                              RightIcon={AvatarIcon}
                              rightDisableStatus={[false, true]}
                              selectInput={(e) => {
                                onChangeInputAmount(e, item)
                              }}
                              leftChange={(selected) => {
                                onChangeToken(selected, item.id, 'inputToken')
                              }}
                              rightChange={(selected) => {
                                onChangeToken(selected, item.id, 'outputToken')
                              }}
                              onMax={() => {
                                onChangeInputAmount({ target: { value: item.inputToken.balance } }, item)
                              }}
                              leftSymbol={item.inputToken.token}
                              rightSymbol={item.outputToken.token}
                            />
                            <p
                              style={{
                                position: 'absolute',
                                bottom: 0,
                                right: index <= 0 ? 73 : 15,
                                color: 'rgba(0, 0, 0, 0.5)',
                                fontSize: 12,
                              }}
                            >
                              {`${1} ${item.inputToken.token}`} ≈ {`${priceStr} ${item.outputToken.token}`}
                            </p>
                          </div>
                          {index > 0 ? (
                            <>
                              <IconButton
                                onClick={() => {
                                  const data = [...columns]
                                  data.splice(index, 1)
                                  if (data.map((it) => it.input > 0).includes(false)) {
                                    setIsSubmit(true)
                                  } else {
                                    setIsSubmit(false)
                                  }
                                  setColumns(data)
                                }}
                                style={{ cursor: 'pointer', marginTop: 8, marginLeft: 10, padding: 0 }}
                              >
                                <DeleteIcon />
                              </IconButton>
                              <IconButton style={{ marginTop: 8, marginLeft: 8, padding: 0 }}>
                                <DroppableIcon style={{ fill: 'none' }} />
                              </IconButton>
                            </>
                          ) : null}
                        </div>
                      )
                    }}
                  </Draggable>
                )
              })}
              {provided.placeholder}
            </div>
          )
        }}
      </Droppable>
    </DragDropContext>
    <AddBtn onClick={addMethods}>
      <AddIcon />
    </AddBtn>
    <DataWrapper
      data={[
        [
          DataIndexName.CollateralRatio,
          collateralIndex.displayRatio,
          `${!(newCollateralRatio === 0 || newCollateralRatio === Number.POSITIVE_INFINITY) ? '≈' : ''
          } ${newDisplayCollateralRatio}`,
        ],

        [
          DataIndexName.PrepaidInterestCoverage,
          collateralIndex.displayCoverage,
          newDisplayPrepaidInterestCoverage,
        ],
      ]}
    />
    {approveStatus ? (
      <Approve style={{ boxShadow: 'none' }} disabled={isSubmitStatus} onClick={onSubmit}>
        Submit
      </Approve>
    ) : (
      <Approve
        style={{ boxShadow: 'none' }}
        disabled={approveBtnStatus}
        onClick={() => {
          onApprove((state) => {
            setApproveBtnStatus(state)
          })
        }}
      >
        Approve
      </Approve>
    )}
  </>
}

export default function CustomizedAccordions(props) {
  const { onApprove, approveStatus } = props
  const { expanded, handleChange } = usePanel()
  return (
    <Wrapper>
      <Title>Trading through Collateral</Title>
      <Accordion id="panel12" expanded={expanded === 'panel12'} onChange={handleChange('panel12')}>
        <AccordionSummary aria-controls="panel1d-content">
          <Typography>Method 1. Trading through Collateral</Typography>
        </AccordionSummary>
        <AccordionDetails>
          {
            expanded === 'panel12' &&
            <TradingThrough onApprove={onApprove} approveStatus={approveStatus} />
          }
        </AccordionDetails>
      </Accordion>
    </Wrapper>
  )
}
